<template>
  <div id="provincial"></div>
</template>

<script>
import mapData from "@/assets/GeographicData/provincial.js";
export default {
  mounted() {
    let that = this;
    this.echarts.registerMap("SC", mapData);
    let Chart = this.echarts.init(document.getElementById("provincial"));
    let option = {
      get: {
        name: "SC PopEstimates",
        type: "map",
        aspectScale: 1, // 
        // roam: true, //   'scale'  'move'
        map: "SC",
        label: {
          show: true,
          normal: {
            show: true, // 
            fontSize: 12,
            color: "#CFCFCF",
          },
          emphasis: {
            show: true,
            fontSize: 12, // 
            color: "#CFCFCF",
          },
        },
        itemStyle: {
          normal: {
            areaColor: "#000606", //
            borderColor: "#7E8080", //
            borderWidth: 7, // 
            opacity: 1, //
          },
          emphasis: {
            areaColor: "#CD2B29", // 
            borderWidth: 0, // 
          },
        },
        textFixed: {
          Alaska: [20, -20],
        },
      },
      geo: {
        type: "map",
        aspectScale: 1, // 
        // roam: true, //   'scale'  'move'
        map: "SC",
        label: {
          show: true,
          normal: {
            show: true, // 
            fontSize: 12,
            color: "#CFCFCF",
          },
          emphasis: {
            show: true,
            fontSize: 12, // 
            color: "#CFCFCF",
          },
        },
        itemStyle: {
          normal: {
            areaColor: "#000606", //
            borderColor: "#7E8080", //
            borderWidth: 1, // 
            opacity: 1, //
          },
          emphasis: {
            areaColor: "#CD2B29", // 
            borderWidth: 0, // 
          },
        },
        textFixed: {
          Alaska: [20, -20],
        },
        // 
        regions: that.regions,
      },
      series: [
        {
          type: "effectScatter",
          coordinateSystem: "geo",
          symbolSize: 12,
          label: {
            normal: {
              show: false,
            },
            emphasis: {
              show: false,
            },
          },
          itemStyle: {
            normal: {
              shadowBlur: 10,
              color: "#00ECC8",
            },
            emphasis: {
              borderColor: "#fff",
              borderWidth: 1,
            },
          },
        },
      ],
    };
    Chart.setOption(option);
    Chart.on("click", function (params) {
      that.$router.push({
        path: "/city",
        query: { name: params.name },
      });
    });
  },
  data() {
    return {
      regions: [
        {
          //   ,,.
          name: "", // import "./china"  : name: ""()
          itemStyle: {
            normal: {
              opacity: 1, // 
              borderColor: "#fff", // 
              borderWidth: 3, // 
              areaColor: "red", // 
            },
          },
        },
      ],
    };
  },
};
</script>

<style scoped>
#provincial {
  height: 100%;
  width: 100%;
}
</style>
